.poke{
	
	background-color: #badbd3;
	border-radius: 20px;
	text-align: center;
	box-shadow: 5px 5px grey;
	
}
.name{
	font-weight:bold;
}
.maincontainer{
	
	position: relative;
	width: 225px;
	height:310px;
	transform-style: preserve-3d;
	transition: all 0.5s ease;
}


.maincontainer:hover {

	transform: rotateY(180deg);
}

.cardfront{
	position: absolute;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.cardback{
	position: absolute;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	transform: rotateY(180deg);
}

.poke-img{
	width: 150px;
	height: 150px;
	margin: 20px;
}

